import React, { useEffect, useState } from 'react'
// import MyData from './lib/data.json'
import './assets/app.css'
import StoryTextComponent from './component/StoryTextComponent'
import $ from 'jquery'
import Loading from './component/Loading'

function App() {
	const [data, setData] = useState(null)

	useEffect(() => {
		$.getJSON('/wp-json/wp/v2/story_text', (res) => {
			res.map((i) => (i.acf.show_story ? setData(i) : null))
		})
	}, [])

	console.log(data)
	return data ? <StoryTextComponent props={data} /> : <Loading />
}

export default App

